@import "../../styles/variables.scss";

.cartPage {
  background-color: $blue;
  height: 100vh;
  position: relative;

  &__link {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    a {
      text-decoration: none;
      color: $white;
      font-size: 25px;
    }
  }
}
