@import "../../styles/variables.scss";

.notFoundPage {
  background-color: $blue;
  height: 100vh;
  position: relative;

  &__link {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    flex-direction: column;
    text-align: center;

    span {
      font-size: 45px;
      color: $pink;
      margin-bottom: 36px;
    }

    a {
      text-decoration: none;
      color: $white;
      font-size: 25px;
    }
  }
}
