@import "../../styles/variables.scss";
@import "../../styles/mixins.scss";

.collapse-list {
  margin-bottom: 0;
  padding-left: 0;
  list-style: none;
}

.collapse-open {
  display: none;
}

.collapse-painel {
  visibility: hidden;
  max-height: 0;
  opacity: 0;
  transition: max-height 0.1s, visibility 0.3s, opacity 0.3s;
}

.collapse-open:checked ~ .collapse-painel {
  max-height: 100%;
  opacity: 100;
  visibility: visible;
}

.collapse-list li {
  margin-bottom: 0;
}

.collapse-list .collapse-btn {
  cursor: pointer;
  display: block;
  padding: 15px 0 0 0;
  margin-bottom: 0;
  transition: background-color 0.2s ease;
  color: $yellow;
  font-size: 20px;
  cursor: pointer;
  @include LatoExtraBold;

  @include phones {
    font-size: 14px;
  }
}

.collapse-open ~ .collapse-btn:before {
  float: right;
}
