@import "../../styles/variables.scss";
@import "../../styles/mixins.scss";

.footer {
  background-color: $black;
  position: absolute;
  bottom: 0;
  width: 100%;
  padding: 14px 0;

  .container {
    padding: 0;
  }

  &__wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }

  &__logo {
    width: 100%;
    text-align: center;
    transform: rotate(300deg);

    @include tablets {
      transform: rotate(0);
    }

    img {
      max-width: 78px;
      height: auto;
    }
  }

  &__description {
    padding-top: 10px;

    p {
      color: $yellow;
      font-size: 18px;
      line-height: 100%;
      @include LatoExtraBold;
    }
  }
}
