@import "../../../styles/variables.scss";
@import "../../../styles/mixins.scss";

.offerSection {
  padding: 60px 15px 60px 15px;

  @include tablets {
    padding: 54px 15px 54px 15px;
  }

  @include phones {
    padding: 50px 20px 50px 20px;
  }

  .container {
    padding: 0;
  }

  &__main {
    &-wrapper {
      display: grid;
      gap: 40px;
      grid-template-columns: repeat(2, 1fr);

      @include tablets {
        grid-template-rows: 1fr;
        grid-template-columns: 1fr 1fr;
        gap: 30px;
      }

      @include tabletsSmall {
        grid-template-rows: 1fr;
        grid-template-columns: 1fr;
      }
    }
  }

  &__small {
    &-block {
      display: flex;

      @include tablets {
        margin-bottom: 30px;
        flex-direction: column;
      }
    }
  }

  &__title {
    margin-bottom: 40px;

    @include tablets {
      margin-bottom: 30px;
    }

    h2 {
      font-size: 32px;
      color: $white;
      @include LatoExtraBold;

      @include tablets {
        font-size: 24px;
      }

      @include phones {
        font-size: 18px;
      }
    }
  }

  &__bottom {
    margin-top: 60px;

    @include tablets {
      margin-top: 54px;
    }

    @include phones {
      margin-top: 50px;
    }

    &-title {
      h4 {
        color: $white;
        font-size: 32px;
        @include LatoExtraBold;

        @include tablets {
          font-size: 24px;
        }

        @include phones {
          font-size: 18px;
        }
      }
    }

    &-description {
      p {
        color: $white;
        font-size: 20px;
        @include lato;
        opacity: 0.6;

        @include phones {
          font-size: 14px;
        }
      }
    }
  }

  &__wrapper {
    position: relative;

    &::after {
      content: "";
      position: absolute;
      width: 100%;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background: rgba(0, 0, 0, 1);
      z-index: 0;
      opacity: 0.3;
      border-radius: 10px;
    }
  }

  &__longBlock {
    padding: 36px;
    background-size: cover;
    border-radius: 10px;
    background-repeat: no-repeat;
    background-position: 50% 50%;
    position: relative;
    grid-column: span 2;

    &:before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background-color: rgba(0, 0, 0, 0.6);
      border-radius: 10px;
    }

    @include tablets {
      padding: 68px 26px 26px 26px;
      grid-column: span 1;
    }

    @include phones {
      padding: 98px 26px 36px 26px;
    }

    @include smallPhones {
      padding: 69px 26px 36px 26px;
    }

    &-description {
      position: relative;
      z-index: 1;
      padding: 22px 0 22px 0;

      p {
        color: $white;
        font-size: 26px;
        @include LatoExtraBold;

        @include tablets {
          font-size: 16px;
        }
      }
    }

    &-button {
      padding-top: 25px;
    }

    &-title {
      padding-right: 10%;
      position: relative;
      z-index: 1;

      @include phones {
        font-size: 18px;
      }

      h3 {
        font-size: 74px;
        color: $white;
        line-height: 100%;
        z-index: 1;
        @include LatoExtraBold;

        @include tablets {
          font-size: 40px;
        }
      }
    }
  }
}
