@import "../../../styles/variables.scss";
@import "../../../styles/mixins.scss";

.mainSection {
  background-image: url("../../../images/main-bg.png");
  width: 100%;
  height: 100%;
  background-size: cover;
  background-color: $preloaderBackground;
  padding: 59px 0;

  @include tablets {
    padding: 77px 0;
    background-image: url("../../../images/banner-gradient-mobile.png");
  }

  @include phones {
    padding: 106px 0 44px 0;
  }

  &__wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;

    @include tabletsSmall {
      flex-direction: column;
      text-align: center;
    }
  }

  &__left {
    width: 70%;

    @include smallDesktop {
      width: 60%;
      margin-right: 10px;
    }

    @include tabletsSmall {
      width: 100%;
      margin-right: 0;
      padding-top: 140px;
    }

    &-title {
      h1 {
        font-size: 72px;
        line-height: 100%;
        color: $white;
        @include LatoExtraBold;

        @include smallDesktop {
          font-size: 48px;
          line-height: 48px;
        }

        @include tabletsSmall {
          font-size: 36px;
          line-height: 36px;
        }
      }

      span {
        color: $pink;

        @include smallDesktop {
          font-size: 48px;
          line-height: 48px;
        }

        @include tabletsSmall {
          font-size: 36px;
          line-height: 36px;
        }
      }
    }

    &-description {
      padding: 22px 0 22px 0;

      @include phones {
        padding: 16px 0 16px 0;
      }

      p {
        color: $white;
        font-size: 32px;
        @include LatoExtraBold;

        @include tabletsSmall {
          font-size: 16px;
          line-height: 16px;
        }
      }

      span {
        color: $yellow;
      }
    }
  }

  &__right {
    width: 30%;

    @include smallDesktop {
      width: 40%;
      position: relative;
    }

    @include tabletsSmall {
      width: 100%;
      text-align: center;
      order: -1;
    }

    &-img {
      @include tablets {
        position: absolute;
        top: 50%;
        left: 85%;
        transform: translate(-50%, -50%);
      }

      @include tabletsSmall {
        z-index: 0;
        top: 0;
        left: 44%;
        transform: translate(-40%, -50%);
      }

      img {
        width: 327px;
        height: 367px;

        @include tabletsSmall {
          width: 299px;
          height: 336px;
        }
      }
    }
  }
}
