@import "../../styles/variables.scss";
@import "../../styles/mixins.scss";

.smallBlock {
  background-size: cover;
  background-position: 50% 50%;
  border-radius: 10px;
  padding: 30px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: relative;

  &:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.6);
    border-radius: 10px;
  }

  @include tablets {
    padding: 108px 26px 26px 26px;
    margin-right: 0;

    &:last-child {
      margin-bottom: 0;
    }
  }

  @include smallPhones {
    padding: 119px 26px 26px 26px;
  }

  &__title {
    padding-bottom: 22px;
    padding-right: 40%;
    position: relative;
    z-index: 1;

    @include smallDesktop {
      padding-right: 0%;
    }

    h3 {
      color: $white;
      font-size: 46px;
      line-height: 100%;
      @include LatoExtraBold;

      @include tablets {
        font-size: 40px;
      }
    }
  }

  &__description {
    position: relative;
    z-index: 1;

    p {
      color: $white;
      font-size: 24px;
      @include LatoExtraBold;

      @include tablets {
        font-size: 16px;
      }
    }
  }

  &:last-child {
    margin-right: 0;
  }
}
