@import "./mixins.scss";

.button {
  position: relative;

  &--padding {
    padding-top: 22px;
  }

  button {
    padding: 14px 40px;
    border-radius: 100px;
    border: none;
    outline: none;
    font-size: 22px;
    cursor: pointer;
    @include lato;
    transition: opacity 0.25s ease-in-out;

    &:hover {
      opacity: 0.6;
      transition: opacity 0.25s ease-in-out;
    }

    @include phones {
      padding: 12px 40px;
      font-size: 16px;
    }
  }
}
