@import "./styles/mixins.scss";

.container {
  margin: 0 auto;
  max-width: 1076px;
  padding: 0 20px;
}

.wrapper {
  width: 100%;
  overflow: hidden;
  padding-bottom: 133px;
  position: relative;
  min-height: 100%;
  background-image: linear-gradient(45deg, #304571, #0d1e48);

  @include smallDesktop {
    padding-bottom: 127px;
  }
}
