@import "../../styles/mixins.scss";
@import "../../styles/variables.scss";

#menu__toggle {
  opacity: 0;
}

.menu {
  display: none;

  @include phones {
    display: block;
  }

  &__btn {
    display: flex;
    align-items: center;
    position: fixed;
    width: 26px;
    height: 26px;
    cursor: pointer;
    z-index: 1;
    position: absolute;
    top: 50%;
    right: 3px;
    transform: translate(-50%, -50%);
    display: none;

    @include phones {
      display: flex;
    }
  }
}

.menu__btn > span,
.menu__btn > span::before,
.menu__btn > span::after {
  display: block;
  position: absolute;
  width: 100%;
  height: 2px;
  background-color: $white;
  transition: 0.3s;
}
.menu__btn > span::before {
  content: "";
  top: -8px;
}
.menu__btn > span::after {
  content: "";
  top: 8px;
}

.sidebar {
  display: block;
  position: fixed;
  visibility: hidden;
  top: 0;
  left: -100%;
  min-width: 260px;
  height: 100%;
  margin: 0;
  padding: 80px 0;
  list-style: none;
  text-align: center;
  background-color: $white;
  box-shadow: 1px 0px 6px rgba(0, 0, 0, 0.2);
  transition: 0.3s;

  &__item {
    display: block;
    padding: 12px 24px;
    color: #333;
    font-size: 20px;
    text-decoration: none;
    transition: background 0.3s;
    @include lato;
  }
}

#menu__toggle:checked ~ .sidebar {
  visibility: visible;
  left: 0;
}

.menu .sidebar__item:hover {
  background-color: $white;
}

#menu__toggle:checked ~ .menu__btn > span {
  transform: rotate(45deg);
}

#menu__toggle:checked ~ .menu__btn > span::before {
  top: 0;
  transform: rotate(0);
}

#menu__toggle:checked ~ .menu__btn > span::after {
  top: 0;
  transform: rotate(90deg);
}
