@import "../../styles/variables.scss";
@import "../../styles/mixins.scss";

.header {
  width: 100%;
  background-color: $black;
  padding: 14px 0;
  z-index: 1;
  position: relative;

  @include tablets {
    padding: 4px 0;
  }

  &__wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__left {
    width: 50%;

    &-logo {
      width: 104px;
      height: 35px;

      @include tablets {
        width: 71px;
      }

      svg {
        width: 100%;
        height: 100%;
      }
    }
  }

  &__right {
    width: 50%;

    @include phones {
      display: none;
    }

    &-img {
      min-width: 24px;
      height: 20px;

      svg {
        width: 100%;
        height: 100%;
      }
    }

    nav {
      ul {
        display: flex;
        align-items: center;
        justify-content: flex-end;

        li {
          margin-right: 55px;

          @include tabletsSmall {
            margin-right: 35px;
          }

          &:last-child {
            margin-right: 0;
          }

          a {
            color: $white;
            font-size: 18px;
            text-decoration: none;
            display: block;
            @include lato;

            &:hover {
              background-image: linear-gradient(45deg, #ebff00, #ff70d9);
              background-clip: text;
              -webkit-background-clip: text;
              color: transparent;
            }
          }
        }
      }
    }
  }
}
